    <a
      @click="expandWalletAddress = !expandWalletAddress"
      v-if="stakersAddressShort !== null"
    >
      {{ stakersAddressShort }}
      <i v-if="expandWalletAddress === false" class="ri-arrow-down-s-line"></i>
      <i v-else class="ri-arrow-up-s-line"></i>
    </a>
    <a v-else @click="loginMM()"> Login </a>
  </nav>

  <div class="card mb-4" v-if="expandWalletAddress">
