        >
          Follow us on Twitter
        </a>

        <a
          class="dropdown-item"
          @click="expandWalletAddress = false"
          v-bind:href="'https://www.smartscan.cash/address/' + reaperAddress"
          target="_blank"
        >
          Inspect Reaperville Contract Activity
        </a>

        <a
          class="dropdown-item"
          @click="expandWalletAddress = false"
          v-bind:href="
            'https://www.smartscan.cash/address/' + rewardTokenAddress
          "
          target="_blank"
        >
          Inspect SVC Contract Activity
        </a>

        <span
          @click="
            addRewardTokenToMM;
            expandWalletAddress = false;
          "
          class="btn btn-outline-secondary btn-sm"
          ><img src="images/metamask-icon-32.png" />Add Surviville Coin (SVC) to
          Metamask</span
        >
      </div>
    </div>
  </div>

  <h4 v-if="wrongNetwork" class="animate-characters">
    <hr class="my-4" />
    <strong>Please switch the network to {{ netName }}.</strong>
    <hr class="my-4" />
  </h4>

  <router-view />
</template>

<script>
import { ethers } from "ethers";
export default {
  data() {
    return {
      stakersAddressShort: null,

      signer: null,
      provider: null,

      expandWalletAddress: false,

      reaperAddress: window.ReaperAddress,
      rewardTokenAddress: window.RewardTokenAddress,

      netName: window.netName,
      wrongNetwork: false,
    };
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider.getNetwork().then((network) => {
      if (network.chainId !== window.netId) {
        this.wrongNetwork = true;
        window.ethereum
          .request({
            method: "wallet_switchEthereumChain",
            params: [{ chainId: ethers.utils.hexlify(window.netId) }],
          })
          .catch((err) => {
            // This error code indicates that the chain has not been added to MetaMask
            if (err.code === 4902) {
              // dispatch(connectFailed(`Add ${window.netName} to Metamask and then reload this website.`));
              window.ethereum.request({
                method: "wallet_addEthereumChain",
                params: [
                  {
                    chainName: window.netName,
                    chainId: ethers.utils.hexlify(window.netId),
                    nativeCurrency: {
                      name: "BCH",
                      decimals: 18,
                      symbol: "BCH",
                    },
                    rpcUrls: [window.rpcServer],
                    blockExplorerUrls: ["https://smartscan.cash/"],
                  },
                ],
              });
            }
          });
      }
    });
    this.provider.on("network", (n, o) => {
      if (o) {
        window.location.reload();
      }
    });
    this.signer = this.provider.getSigner();
    this.signer.getAddress().then((address) => {
      this.stakersAddressShort =
        address.substring(0, 4) +
        "..." +
        address.substring(address.length - 4, address.length);
    });
  },
  methods: {
    loginMM: function () {
      window.location.reload();
    },

    addRewardTokenToMM: function () {
      window.ethereum.request({
        method: "wallet_watchAsset",
        params: {
          type: "ERC20",
          options: {
            address: window.RewardTokenAddress,
            symbol: "SVC",
            decimals: 18,
          },
        },
      });
    },
  },
};
</script>

